export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			visible: true,
			password: true,
			user: {
				username: '',
				password: ''
			}
		}
	},
	methods: {

		// 微信小程序登录
		weChatLogin() {
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					console.log(infoRes.userInfo)
					uni.login({
						success: function(res) {
							// 获取code  
							uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
							// console.log(res.code, infoRes.userInfo)
							_this.weixinLogin(res.code, infoRes.userInfo);
						}
					});


				},
				fail(res) {}
			});
		},
		weixinLogin(code, e) {
			console.log('1111')
			uni.showLoading()
			uni.request({
				url: `https://www.imovietrailer.com/superhero/stu/mpWXLogin/${code}?qq=2684425594`,
				method: 'POST',
				data: {
					wxUserBO: e,
					avatarUrl: e.avatarUrl,
					nickName: e.nickName,
					whichMP: 1,
					// qq:'434714873'

				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {
					console.log(res)
					if (res.statusCode === 200) {
						// this.movieDetails = res.data.data

					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
































































		//h5注册登录
		login() {
			uni.showLoading()
			uni.request({
				url: `https://www.imovietrailer.com/superhero/user/registOrLogin?qq=2684425594`,
				method: 'POST',
				data: {
					username: this.user.username,
					password: this.user.password,
					userBO: this.user

				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {

					if (res.statusCode === 200) {
						if (res.data.status === 200) {
							uni.setStorageSync('userInfo', res.data.data)
							console.log(res.data.data)
							this.$store.state.userInfo = res.data.data
							uni.switchTab({
								url: '../my/my'
							})
							uni.showToast({
								title: '登录/注册成功'
							})
						} else {

							uni.showToast({
								title: res.data.msg,
								icon: "none"
							})
						}
						// console.log(res)
						// this.movieDetails = res.data.data

					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		}
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
